.company-goods {
  background-color: #1b2d37;

  width: 100%;
  min-height: 500px;

  a {
    color: #ffffff;
  }
}

.company-goods__wrapper {
  max-width: 1600px;
}

.company-goods h2 {
  display: block;

  margin: 0;
  margin-bottom: 47px;
  margin-left: calc(13% + 5px);
  padding-top: 88px;

  font-family: $title-font;
  font-size: 36px;
  font-weight: 400;
  line-height: 51px;
  color: $color-title;
}

.company-goods__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  margin: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  padding-bottom: 97px;

  list-style: none;
}

.company-goods__item {
  position: relative;
  flex-grow: 1;

  max-width: 23%;
  min-height: 625px;

  padding-bottom: 10px;
  margin-bottom: 30px;

  font-family: $title-font;
  font-size: 20px;
  line-height: 30px;

  &::before {
    content: "";
    position: absolute;
    z-index: 0;

    left: 0;
    top: 0;

    width: 100%;
    height: 100%;

    background: rgba(27, 45, 55, 0.8);
  }

  &::after {
    content: "";
    position: absolute;

    right: 0;
    bottom: 1px;

    width: 63px;
    height: 63px;

    background: url("../img/pointer.svg") no-repeat center $pick-out-color;
  }

  &--make {
    background: url("../img/bg-make.jpg") no-repeat;
    background-size: cover;
  }

  &--edit {
    background: url("../img/bg-edit.jpg") no-repeat;
    background-size: cover;
  }

  &--details {
    background: url("../img/bg-details.jpg") no-repeat;
    background-size: cover;
  }

  &:hover {
    opacity: 0.5;
  }
}

li.company-goods__item + li {
  margin-left: 2%;
}

.company-goods__item a {
  position: relative;
  display: block;
  z-index: 1;

  width: 100%;
  height: 100%;

  padding-top: 55px;
  padding-left: 44px;
  padding-right: 134px;
}

@media (max-width: $tablet-width) {
  .company-goods__list {
    flex-direction: column;

    padding-bottom: 79px;
  }

  li.company-goods__item + li {
    margin-left: 26px;
    margin-top: 46px;
  }

  .company-goods__item {
    max-width: 100%;
    min-height: 256px;

    margin-left: 26px;
    margin-right: 24px;
    margin-bottom: 0;

    font-size: 20px;
    line-height: 30px;

    background-size: 100%;
    background-position: center;

    &--make {
      background: url("../img/bg-make-tablet.jpg") no-repeat;
      background-size: cover;
    }
  }

  .company-goods h2 {
    font-size: 32px;
    line-height: 45px;

    padding-left: 26px;
    padding-top: 50px;
    padding-right: 120px;

    margin-bottom: 49px;
    margin-left: 0;
  }

  .company-goods__item a {
    padding-top: 42px;
    padding-right: 76%;
  }

  .company-goods__item--details a {
    padding-top: 52px;
  }
}

@media (max-width: $mobile-width) {
  .company-goods__list {
    padding-bottom: 65px;
  }

  .company-goods h2 {
    font-size: 28px;
    line-height: 39px;

    margin-bottom: 42px;
    padding-right: 0;
  }

  .company-goods__item {
    margin-left: 15px;
    margin-right: 15px;

    &::after {
      bottom: -1px;
    }
  }

  li.company-goods__item + li {
    margin-left: 15px;
    margin-top: 30px;
  }

  .company-goods__item a {
    padding-top: 20px;
    padding-left: 35px;
    padding-right: 76%;
  }

  .company-goods__item--details a {
    padding-top: 30px;
  }
}
